import React from 'react';
import { List, Avatar, Space } from 'antd';
import { MessageOutlined, LikeOutlined, StarOutlined } from '@ant-design/icons';
import googleImage from "../files/images/google.jpeg";


interface Props {
    icon : any,
    text : string
}

interface PublicationEntry {
    href : string,
    title : string,
    avatar? : string,
    description : string,
    content : string
}

const Publication = () => {
    const listData = [];
   const icip2020 : PublicationEntry = {
       href : 'link',
       title : 'A Comparative Evaluation of Temporal Pooling Methods for Blind Video Quality Assessment',
       description:
           'Ant Design, a design language for background applications, is refined by Ant UED Team.',
       content:
           'We supply a series of design principles, practical patterns and high quality design resources (Sketch and Axure), to help people create their product prototypes beautifully and efficiently.',

   }

    listData.push(icip2020, icip2020, icip2020, icip2020, icip2020)



    const IconText = (props : Props) => (
        <Space>
            {React.createElement(props.icon)}
            {props.text}
        </Space>
    );

    return (
        <div>
            <List
                itemLayout="vertical"
                size="large"
                pagination={{
                    onChange: page => {
                        console.log(page);
                    },
                    pageSize: 3,
                }}
                dataSource={listData}
                footer={
                    <div>
                        <b>Zhengzhong Tu</b> all publications
                    </div>
                }
                renderItem={item => (
                    <List.Item
                        key={item.title}
                        actions={[
                            <IconText icon={StarOutlined} text="dblb" key="list-vertical-star-o" />,
                            <IconText icon={LikeOutlined} text="arXiv" key="list-vertical-like-o" />,
                        ]}
                        extra={
                            <img
                                width={150}
                                alt="logo"
                                src={googleImage}
                            />
                        }
                    >
                        <List.Item.Meta
                            title={<a href={item.href}>{item.title}</a>}
                            description={item.description}
                        />
                        {item.content}
                    </List.Item>
                )}
            />,
        </div>
    )
}

export default Publication